
import { defineComponent } from "vue";

export default defineComponent({
    name: "ModalWindow",
    emits: ['update:modelValue'],
    props: {
        modelValue: {
            type: Boolean,
            required: true
        },
        title: {
            type: String,
            default: ''
        }
    },
    methods: {
        close() {
            this.$emit('update:modelValue', false)
        }
    }

})
