
import {defineComponent, PropType} from "vue";
import {MainParam} from "@/types";

export default defineComponent({
    name: "MainBar",
    props: {
        value: {
            type: Object as PropType<MainParam>,
            required: true
        }
    },
    computed: {
        percent() {
            return this.value.current / this.value.max * 100
        }
    }
})
